import { MobileWallet } from "@web3modal/core";
import { DesktopWallet } from "@web3modal/core/dist/_types/src/types/controllerTypes";
import {
  EthereumClient,
  w3mConnectors,
  w3mProvider,
} from "@web3modal/ethereum";
import { Web3Button, Web3Modal } from "@web3modal/react";
import { configureChains, createClient, WagmiConfig } from "wagmi";
import { arbitrum, mainnet, polygon } from "wagmi/chains";
import ReactJsonPrint from "react-json-print";
import { Col, Row } from "react-flexbox-grid";
const chains = [arbitrum, mainnet, polygon];
const projectId = `d7f156f22c6a01b54985f90a18fc0193`;

const mobileWallets: MobileWallet[] = [
  {
    id: "c57ca95b47569778a828d19178114f4db188b89b763c899ba0be274e97267d96",
    links: {
      universal: "",
      native: `https://metamask.app.link/dapp/${window.location.hostname}/web3`,
    },
    name: "MetaMask",
  },
];

const walletImages: Record<string, string> = {
  c57ca95b47569778a828d19178114f4db188b89b763c899ba0be274e97267d96:
    "https://revision-team.github.io/web3/metamask.png",
};

const { provider } = configureChains(chains, [w3mProvider({ projectId })]);

const wagmiClient = createClient({
  autoConnect: true,
  connectors: w3mConnectors({ projectId, version: 1, chains }),
  provider,
});
const ethereumClient = new EthereumClient(wagmiClient, chains);

function HomePage() {
  return <Web3Button />;
}

function App() {
  console.log({ mobileWallets });

  return (
    <>
      <WagmiConfig client={wagmiClient}>
        <HomePage />
      </WagmiConfig>
      <ReactJsonPrint dataObject={{ mobileWallets, walletImages }} expanded />
      <Web3Modal
        projectId={projectId}
        ethereumClient={ethereumClient}
        mobileWallets={mobileWallets}
        walletImages={walletImages}
      />
    </>
  );
}

export default App;
